.thanks .header{
    width: 100%;
}
.thanks .header img{
    width: 10%;
    float: right;
    margin-top: 6%;
    padding-right: 5%;
}
.thanks .body {
    text-align: center;
    display: grid;
    justify-items: center;
    align-items: center;
    height: 60vh;
    margin: auto;
}
.thanks .body h1{
 font-family: 'Glacial-Bold';
}
.thanks .survey{
    margin: auto;
    text-align: center;
    justify-items: center;
    align-items: center;
}
.thanks .survey p+p {
    margin-top: -10%;
    margin-right: 10%;
}
.thanks .survey p{
font-family: 'Glacial-Regular';
}
.thanks .survey img {
    width: 10%;
    margin-left: 5%;
}

.thanks .survey a{
    font-family: 'Glacial-Bold';
    color: #000;
    text-decoration: underline;
}
.footer{
  display: grid;
  align-items: center;
  justify-items: center;
  text-align: center;
}
.footer span {
    font-family: 'Glacial-Bold';
}
.footer .social-media{
    display: inline;
    list-style: none;
}
.footer .social-media li {
    display: inline-block;
    padding: 10px;
}
.footer .social-media li a {
    font-size: 35px;
}
.footer .social-media li:nth-child(1) a {
    color: #000;
}
@media screen and (max-width: 600px) {
    .thanks .header{
        width: 100%;
    }
    .thanks .header img{
        width: 12%;
        float: right;
        margin-top: 6%;
        padding-right: 5%;
    }
    .thanks .body {
        text-align: center;
        display: grid;
        justify-items: center;
        align-items: center;
        height: 60vh;
        margin: auto;
    }
    .thanks .body h1{
     font-family: 'Glacial-Bold';
    }
    .thanks .survey{
        margin: auto;
        text-align: center;
        justify-items: center;
        align-items: center;
    }
    .thanks .survey p+p {
        margin-top: -10%;
        margin-right: 10%;
    }
    .thanks .survey p{
    font-family: 'Glacial-Regular';
    }
    .thanks .survey img {
        width: 10%;
        margin-left: 5%;
    }
    
    .thanks .survey a{
        font-family: 'Glacial-Bold';
        color: #000;
        text-decoration: underline;
    }
    .footer {
        margin: auto !important;
        margin-top: 5% !important;
    }

}
